<template>
  <div>
    <div class="unit-contributors">
      <contributor v-for="contributor in contributors"
                   :key="contributor.id"
                   :contributor="contributor" />
    </div>
    <div class="show-all">
      <a href="javascript:;"
         v-if="this.all.length > 10"
         @click="onClickToggleShowAll">{{ showAllText }}</a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import Contributor from '../parts/Contributor.vue';

export default {
  data() {
    return {
      showAll: false,
    };
  },
  components: {
    Contributor,
  },
  computed: {
    ...mapGetters('contributors', ['all', 'top10']),
    showAllText() {
      return this.showAll ? 'Hide' : 'Show All Contributors';
    },
    contributors() {
      return this.showAll ? this.all : this.top10;
    },
  },
  methods: {
    onClickToggleShowAll() {
      this.showAll = !this.showAll;
    },
  },
};
</script>

<style lang="scss" scoped>
.unit-contributors {
  display: flex;
  flex-wrap: wrap;
}
.show-all {
  text-align: right;
  margin-right: 10px;
  a {
    color: #ccc;
    font-size: 13px;
    &:hover {
      text-decoration: none;
    }
  }
}
</style>



