import _ from 'lodash';
import Vue from 'vue';

export default {
  namespaced: true,
  state: {
    all: [],
  },
  mutations: {
    add_tag(state, { tag }) {
      state.all.push(tag);
    },
    count_up(state, { targetIndex, stock }) {
      state.all[targetIndex].hasStocks.push(stock.id);
    },
    select_tag(state, { targetIndex }) {
      Vue.set(state.all, targetIndex, {
        ...state.all[targetIndex],
        selected: true,
      });
    },
    deselect_tag(state, { targetIndex }) {
      Vue.set(state.all, targetIndex, {
        ...state.all[targetIndex],
        selected: false,
      });
    },
  },
  actions: {
    addTag({ state, commit }, { tag, stock }) {
      const targetIndex = _.findIndex(state.all, t => t.name === tag.name);
      if (targetIndex < 0) {
        // タグがない場合
        tag.hasStocks = [stock.id];
        tag.selected = false;
        commit('add_tag', { tag });
      } else {
        // すでにタグがある場合
        const target = state.all[targetIndex];
        if (_.findIndex(target.hasStocks, id => id === stock.id) < 0) {
          commit('count_up', { targetIndex, stock });
        }
      }
    },
    addTagByStocks({ dispatch }, { stocks }) {
      _.each(stocks, (stock) => {
        if (_.isArray(stock.tags) && stock.tags.length > 0) {
          _.each(stock.tags, (tag) => {
            dispatch('addTag', { tag, stock });
          });
        }
      });
    },
    toggleSelected({ state, commit }, { tag }) {
      const targetIndex = _.findIndex(state.all, t => t.name === tag.name);
      if (state.all[targetIndex].selected) {
        commit('deselect_tag', { targetIndex });
      } else {
        commit('select_tag', { targetIndex });
      }
    },
  },
  getters: {
    all: state => _.orderBy(state.all, [tag => tag.hasStocks.length], ['desc']),
    top10: (state, getters) => _(getters.all).orderBy([tag => tag.hasStocks.length], ['desc']).take(10).value(),
    selectedTags: state => _.filter(state.all, tag => tag.selected),
  },
};



