<template>
  <a class="stock-item" :href="stock.url" target="_blank">
    <div class="user">
      <span class="ph"
            :style="{ 'background-image': `url(${stock.user.profile_image_url})` }"></span>
      <span class="text">
        <span class="name">{{ stock.user.name ? stock.user.name : stock.user.id }}</span>
        {{ createAt }}
      </span>
    </div>
    <p class="title">{{ stock.title }}</p>
  </a>
</template>

<script>
import moment from 'moment';

export default {
  props: ['stock'],
  computed: {
    createAt() {
      return moment(this.stock.created_at).format('YYYY/MM/DD HH:mm');
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/theme.scss';

a.stock-item {
  border-radius: 3px;
  text-decoration: none;
  color: #333;
  box-shadow: 0 2px 4px 1px rgba(0, 0, 0, .1);
  display: block;
  border: 1px solid #ccc;
  padding: 5px;
  flex: 1;
  transition: all .3s;
  &:hover {
    box-shadow: 0 3px 8px 1px rgba(0, 0, 0, .3);
  }
}

.user {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  .ph {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    overflow: hidden;
    background-size: cover;
    border: 2px solid $primary;
  }
  .text {
    font-size: 12px;
    margin-left: 10px;
    line-height: 1.2;
  }
  .name {
    display: block;
    font-weight: bold;
    margin-bottom: 2px;
  }
}

.title {
  padding: 5px;
  font-size: 13px;
  line-height: 1.3;
}
</style>



