<template>
  <a href="javascript:;" :class="className" @click="onClick">
      <span class="ph"
            :style="{ 'background-image': `url(${contributor.profile_image_url})` }"></span>
    <span class="name">
      {{ contributor.name ? contributor.name : contributor.id }}({{ contributor.hasStocks.length }})
    </span>
  </a>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  props: ['contributor'],
  computed: {
    className() {
      const classes = ['contributor'];
      if (this.contributor.selected) {
        classes.push('selected');
      }
      return classes;
    },
  },
  methods: {
    ...mapActions('contributors', ['toggleSelected']),
    onClick() {
      this.toggleSelected({ contributor: this.contributor });
    },
  },
};
</script>

<style lang="scss" scoped>
a.contributor {
  position: relative;
  width: 25%;
  font-size: 11px;
  color: #fff;
  text-decoration: none;
  text-align: center;
  line-height: 1.1;
  margin-bottom: 12px;
  box-sizing: border-box;
  padding: 0 2px;

  .ph {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-size: cover;
    &:after {
      content: " ";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 100%;
      border: 3px solid #af0045;
      box-sizing: border-box;
      opacity: 0;
    }
  }

  .name {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    padding: 4px 8px;
    left: 0;
    bottom: 100%;
    margin-bottom: 4px;
    background: rgba(0, 0, 0, .8);
    display: none;
    font-weight: bold;
    word-break: break-all;
  }
  &:hover {
    .ph {
      opacity: .7;
    }
    .name {
      display: block;
    }
  }

  &.selected {
    .ph {
      &:after {
        opacity: 1;
      }
    }
  }
}
</style>



