import axios from 'axios';
import _ from 'lodash';
import * as config from '../../config';
import * as types from './mutations_types';
import * as storage from '../../utils/localStorage';

/**
 * ストックの整形
 * @param stock
 * @returns {*}
 */
function filterStock(stock) {
  // 不要なキーを削除して容量削減
  delete stock.body;
  delete stock.coediting;
  delete stock.group;
  delete stock.private;
  delete stock.user.facebook_id;
  delete stock.user.followees_count;
  delete stock.user.followers_count;
  delete stock.user.github_login_name;
  delete stock.user.items_count;
  delete stock.user.linkedin_id;
  delete stock.user.location;
  delete stock.user.organization;
  delete stock.user.permanent_id;
  delete stock.user.twitter_screen_name;
  delete stock.user.website_url;

  stock.rendered_body = stock.rendered_body.replace(/<("[^"]*"|'[^']*'|[^'">])*>/g, '');
  stock.rendered_body = stock.rendered_body.slice(0, 99);
  return stock;
}

function fetchAllStocksPromise({ userId }) {
  return new Promise((resolve, reject) => {
    let stocks = [];

    function fetch({ page, perPage }) {
      axios.get(`${config.ApiBaseUrl}/users/${userId}/stocks`, {
        params: { page, per_page: perPage },
      })
        .then((response) => {
          if (_.isArray(response.data) && response.data.length > 0) {
            stocks = stocks.concat(_.map(response.data, stock => filterStock(stock)));
            fetch({ page: page + 1, perPage });
          } else {
            // end
            resolve(stocks);
          }
        })
        .catch((e) => {
          e.msg = 'Qiita APIへの接続に失敗しました。';
          reject(e);
        });
    }

    // 再帰呼出し
    fetch({ page: 1, perPage: config.allStocksLoadPerPage });
  });
}

export default {
  /**
   * Qiita APIから全ストックを取得する
   * @param state
   * @param commit
   * @param dispatch
   * @param userId
   */
  fetchAllStocks({ state, commit, dispatch }, { userId }) {
    commit(types.FETCH_STOCKS_START);
    dispatch('loading/startLoading', { msg: 'ストック記事再取得中' }, { root: true });
    fetchAllStocksPromise({ userId })
      .then((stocks) => {
        commit(types.APPEND_STOCKS, { stocks });
        storage.setItem('stocks', state.all)
          .then(() => {
            dispatch('loading/stopLoading', null, { root: true });
            dispatch('tags/addTagByStocks', { stocks }, { root: true });
            dispatch('contributors/addContributorByStocks', { stocks }, { root: true });
            commit(types.FETCH_STOCKS_SUCCESS);
          })
          .catch((e) => {
            dispatch('loading/stopLoading', null, { root: true });
            e.msg = 'ローカルストレージへの書き込みに失敗しました。';
          });
      })
      .catch((e) => {
        dispatch('loading/stopLoading', null, { root: true });
        commit(types.FETCH_STOCKS_FAIL, e);
      });
  },

  /**
   * 全ストック取得のPromise版
   * @param state
   * @param commit
   * @param dispatch
   * @param userId
   */
  fetchAllStocksWithPromise({ state, commit, dispatch }, { userId }) {
    return new Promise((resolve, reject) => {
      fetchAllStocksPromise({ userId })
        .then((stocks) => {
          commit(types.APPEND_STOCKS, { stocks });
          storage.setItem('stocks', state.all)
            .then(() => {
              dispatch('tags/addTagByStocks', { stocks }, { root: true });
              dispatch('contributors/addContributorByStocks', { stocks }, { root: true });
              resolve();
            })
            .catch((e) => {
              e.msg = 'ローカルストレージへの書き込みに失敗しました。';
              reject(e);
            });
        })
        .catch((e) => {
          reject(e);
        });
    });
  },

  loadStocks({ commit, dispatch }) {
    commit(types.LOAD_STOCKS_START);
    storage.getItem('stocks')
      .then((stocks) => {
        if (stocks) {
          commit(types.APPEND_STOCKS_FROM_STORAGE, { stocks });
          dispatch('tags/addTagByStocks', { stocks }, { root: true });
          dispatch('contributors/addContributorByStocks', { stocks }, { root: true });
        }
        commit(types.LOAD_STOCKS_SUCCESS);
      })
      .catch(() => {
        commit(types.LOAD_STOCKS_FAIL, { msg: 'ローカルストレージの読み込みに失敗しました。' });
      });
  },

  /**
   * Qiita APIからストックを取得する
   * @param commit
   * @param dispatch
   * @param userId
   * @param page
   * @param perPage
   */
  fetchStocks({ commit, dispatch }, { userId, page, perPage }) {
    commit(types.FETCH_STOCKS_START);
    axios.get(`${config.ApiBaseUrl}/users/${userId}/stocks`, {
      params: { page, per_page: perPage },
    })
      .then((response) => {
        if (_.isArray(response.data) && response.data.length > 0) {
          commit(types.APPEND_STOCKS, _.map(response.data, stock => filterStock(stock)));
        }
        dispatch('tags/addTagByStocks', { stocks: response.data }, { root: true });
        dispatch('contributors/addContributorByStocks', { stocks: response.data }, { root: true });
        commit(types.FETCH_STOCKS_SUCCESS);
      })
      .catch((e) => {
        e.msg = 'Qiita APIへの接続に失敗しました。';
        commit(types.FETCH_STOCKS_FAIL);
      });
  },
};



