<template>
  <transition name="fade">
    <div v-show="loading" class="loading">
      <div class="loader"></div>
      <p class="message">{{ loadingMsg }}</p>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      dots: '',
      loadingMsg: '',
    };
  },
  computed: {
    ...mapGetters('loading', ['loading', 'msg']),
  },
  watch: {
    dots() {
      if (this.msg === '') {
        this.loadingMsg = '';
        return;
      }

      this.loadingMsg = this.msg + this.dots;
    },
  },
  created() {
    setInterval(() => {
      if (this.dots.length < 3) {
        this.dots += '.';
      } else {
        this.dots = '';
      }
    }, 500);
  },
};
</script>

<style lang="scss" scoped>
.loading {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, .7);
}

.message {
  position: absolute;
  height: 0;
  width: 100%;
  margin: auto;
  top: 0;
  bottom: 0;
  text-align: center;
  padding-top: 80px;
  font-size: 13px;
}

$color-1: #00a7ce;
$color-2: #72c45a;
$color-3: #f963a3;
$color-4: #ffd15b;

$ball_size: 20px;
$jump: 15px;
$diam: 2 * $jump;
$duration_val: 1.2s;

.loader {
  position: absolute;
  width: 0;
  height: 0;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation: anim_rotate $duration_val infinite ease;

  &:before,
  &:after {
    content: '';
    position: absolute;
    width: $ball_size;
    height: $ball_size;
    top: 50%;
    left: 50%;
    margin-top: -($ball_size / 2);
    margin-left: -($ball_size / 2);
    border-radius: ($ball_size / 2);
    opacity: .9;
  }

  &::before {
    background: $color-1;
    transform: translate(- $jump, - $jump) scale(1);
    box-shadow: $diam $diam 0 $color-2;
    animation: anim_1_2 $duration_val infinite ease;
  }

  &:after {
    background: $color-3;
    transform: translate($jump, - $jump) scale(1);
    box-shadow: - $diam $diam 0 $color-4;
    animation: anim_3_44 $duration_val infinite ease;
  }

  @keyframes anim_1_2 {
    50% {
      transform: scale(1.3) translate(0, 0);
      box-shadow: 0 0 0 $color-2;
      opacity: .8;
    }
  }

  @keyframes anim_3_44 {
    50% {
      transform: scale(1.3) translate(0, 0);
      box-shadow: 0 0 0 $color-4;
      opacity: .8;
    }
  }

  @keyframes anim_rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(360deg);
    }
    100% {
      transform: rotate(720deg);
    }
  }
}
</style>



