import _ from 'lodash';
import * as types from './mutations_types';

export default {
  [types.FETCH_STOCKS_START](state) {
    state.fetching = true;
    state.error = false;
    state.msg = '';
  },
  [types.FETCH_STOCKS_SUCCESS](state) {
    state.fetching = false;
  },
  [types.FETCH_STOCKS_FAIL](state, { msg }) {
    state.fetching = false;
    state.error = true;
    state.msg = msg;
  },
  [types.LOAD_STOCKS_START](state) {
    state.loading = true;
    state.error = false;
    state.msg = '';
  },
  [types.LOAD_STOCKS_SUCCESS](state) {
    state.loading = false;
  },
  [types.LOAD_STOCKS_FAIL](state, { msg }) {
    state.loading = false;
    state.error = true;
    state.msg = msg;
  },
  [types.APPEND_STOCKS](state, { stocks }) {
    const { all } = state;
    _(stocks).each((stock, i) => {
      const sameStockIndex = _.findIndex(state.all, s => s.id === stock.id);
      if (sameStockIndex >= 0) {
        all[sameStockIndex] = {
          ...all[sameStockIndex],
          ...stock,
        };
      } else {
        all.splice(i, 0, {
          ...stock,
          stoqiiTags: [],
        });
      }
    });
    state.all = all;
  },
  [types.APPEND_STOCKS_FROM_STORAGE](state, { stocks }) {
    state.all = stocks;
  },
};



