export default {
  namespaced: true,
  state: {
    loading: false,
    msg: '',
  },
  mutations: {
    loading_start: (state) => {
      state.loading = true;
    },
    loading_end: (state) => {
      state.loading = false;
    },
    set_msg: (state, { msg }) => {
      state.msg = msg;
    },
    remove_msg: (state) => {
      state.msg = '';
    },
  },
  actions: {
    startLoading({ commit }, { msg }) {
      commit('loading_start');
      if (msg) {
        commit('set_msg', { msg });
      }
    },
    stopLoading({ commit }) {
      commit('loading_end');
      commit('remove_msg');
    },
    setMsg({ commit }, { msg }) {
      commit('set_msg', { msg });
    },
    removeMsg({ commit }) {
      commit('remove_msg');
    },
  },
  getters: {
    msg: state => state.msg,
    loading: state => state.loading,
  },
};



