<template>
  <div class="sidebar">
    <input class="search" type="text" v-model="searchWord" placeholder="Search">
    <div class="sidebar-row">
      <h2 class="sidebar-heading">- Tags</h2>
      <unit-tags />
    </div>
    <div class="sidebar-row">
      <h2 class="sidebar-heading">- Contributors</h2>
      <unit-contributors />
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import UnitTags from '../units/UnitTags.vue';
import UnitContributors from '../units/UnitContributors.vue';

export default {
  data() {
    return {
      searchWord: '',
    };
  },
  components: {
    UnitTags,
    UnitContributors,
  },
  computed: {
    ...mapGetters('search', ['word']),
  },
  methods: {
    ...mapActions('search', ['updateWord']),
  },
  watch: {
    searchWord(newWord) {
      this.updateWord({ word: newWord });
    },
  },
  created() {
    this.searchWord = this.word;
  },
};
</script>

<style lang="scss" scoped>
@import '../../styles/mixins.scss';
@import '../../styles/theme.scss';

.sidebar {
  padding: 5px;
  .search {
    margin-top: 5px;
    margin-bottom: 10px;
    border: none;
    background: shade($primary, 5);
    width: 100%;
    box-sizing: border-box;
    padding: 10px 8px;
    color: #fff;
    font-weight: 300;
    @include PlaceHolderColor(#bbb, italic, 300);
    &:focus {
      background: tint($primary, 10);
    }
  }
  .sidebar-row {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .sidebar-heading {
    color: #fff;
    font-size: 16px;
    font-style: italic;
    font-weight: 300;
    margin-bottom: 5px;
  }
}
</style>



