<template>
  <div>
      <div class="unit-tags">
        <transition-group name="fade">
          <base-tag v-for="tag in tags" :key="tag.name" :tag="tag" />
        </transition-group>
      </div>
    <div class="show-all">
      <a href="javascript:;"
         v-if="this.all.length > 10"
         @click="onClickToggleShowAll">{{ showAllText }}</a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseTag from '../parts/BaseTag.vue';

export default {
  data() {
    return {
      showAll: false,
    };
  },
  components: {
    BaseTag,
  },
  computed: {
    ...mapGetters('tags', ['all', 'top10']),
    tags() {
      return this.showAll ? this.all : this.top10;
    },
    showAllText() {
      return this.showAll ? 'Hide' : 'Show All Tags';
    },
  },
  methods: {
    onClickToggleShowAll() {
      this.showAll = !this.showAll;
    },
  },
};
</script>

<style lang="scss" scoped>
.unit-tags {
  margin-left: -2px;
  margin-right: -2px;
}
.show-all {
  text-align: right;
  margin-right: 10px;
  a {
    color: #ccc;
    font-size: 13px;
    &:hover {
      text-decoration: none;
    }
  }
}
</style>



