<template>
  <div class="home">
    <div>
      <p v-show="error && msg">{{ msg }}</p>
    </div>
    <stock-list :stocks="sortedAll" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import StockList from '../StockList.vue';

export default {
  name: 'Home',
  components: {
    StockList,
  },
  methods: {
    ...mapActions('stocks', ['loadStocks']),
  },
  computed: {
    ...mapGetters('user', ['userId']),
    ...mapGetters('stocks', ['sortedAll', 'error', 'msg']),
  },
  created() {
    this.loadStocks();
  },
};
</script>



