import axios from 'axios/index';
import * as config from '../../config';
import router from '../../router';

export default {
  namespaced: true,
  state: {
    userId: '',
    // ユーザーIDの有効性
    isAvailable: true,
    loading: false,
    msg: '',
  },
  mutations: {
    update_user_id: (state, { userId }) => {
      state.userId = userId;
    },
    set_available: (state, { available, msg }) => {
      state.isAvailable = available;
      state.msg = msg;
    },
    check_start: (state) => {
      state.loading = true;
    },
    check_end: (state) => {
      state.loading = false;
    },
  },
  actions: {
    updateUserId({ commit }, { userId }) {
      commit('update_user_id', { userId });
    },
    checkUserId({ getters, commit, dispatch }) {
      commit('check_start');
      commit('set_available', { available: true, msg: '' });
      axios.get(`${config.ApiBaseUrl}/users/${getters.userId}`)
        .then((response) => {
          if (response.data && response.data.id) {
            dispatch('user/setUserId', { userId: response.data.id }, { root: true })
              .then(() => {
                dispatch('loading/startLoading', { msg: 'ストック記事取得中' }, { root: true });
                return dispatch('stocks/fetchAllStocksWithPromise', { userId: response.data.id }, { root: true });
              })
              .then(() => {
                dispatch('loading/stopLoading', null, { root: true });
                router.push('/');
              })
              .catch((e) => {
                dispatch('loading/stopLoading', null, { root: true });
                e.available = false;
                commit('set_available', e);
              });
          }
          commit('check_end');
        })
        .catch((error) => {
          dispatch('loading/stopLoading', null, { root: true });
          if (error.response && error.response.status === 404) {
            commit('set_available', { available: false, msg: 'ユーザーIDが有効ではありませんでした。' });
          } else {
            commit('set_available', { available: false, msg: 'Qiita APIへの接続に失敗しました。' });
          }
          commit('check_end');
        });
    },
  },
  getters: {
    userId: state => state.userId,
    isAvailable: state => state.isAvailable,
    loading: state => state.loading,
    msg: state => state.msg,
  },
};



