import _ from 'lodash';

export default {
  all: state => state.all,
  fetching: state => state.fetching,
  loading: state => state.loading,
  error: state => state.error,
  msg: state => state.msg,
  sortedAll: (state, getters, rootState, rootGetters) => {
    const { all } = getters;
    const selectedTags = rootGetters['tags/selectedTags'];
    const selectedContributors = rootGetters['contributors/selectedContributors'];
    const searchWord = rootGetters['search/word'];
    let stocks = all;

    if (selectedTags.length > 0) {
      stocks = _(stocks).filter((stock) => {
        let include = false;
        // eslint-disable-next-line
        _.each(selectedTags, (selectTag) => {
          if (_.findIndex(stock.tags, tag => tag.name === selectTag.name) >= 0) {
            include = true;
            return false;
          }
        });
        return include;
      }).value();
    }

    if (selectedContributors.length > 0) {
      stocks = _(stocks).filter((stock) => {
        let include = false;
        // eslint-disable-next-line
        _.each(selectedContributors, (selectContributor) => {
          if (stock.user.id === selectContributor.id) {
            include = true;
            return false;
          }
        });
        return include;
      }).value();
    }

    if (searchWord !== '') {
      const re = new RegExp(searchWord, 'gi');
      stocks = _(stocks).filter((stock) => {
        const titleMatch = stock.title.match(re);
        const bodyMatch = stock.rendered_body.match(re);
        return titleMatch || bodyMatch;
      }).value();
    }

    return stocks;
  },
};



