import _ from 'lodash';
import Vue from 'vue';

export default {
  namespaced: true,
  state: {
    all: [],
  },
  mutations: {
    add_contributor(state, { contributor }) {
      state.all.push(contributor);
    },
    count_up(state, { targetIndex, stock }) {
      state.all[targetIndex].hasStocks.push(stock.id);
    },
    select_contributor(state, { targetIndex }) {
      Vue.set(state.all, targetIndex, {
        ...state.all[targetIndex],
        selected: true,
      });
    },
    deselect_contributor(state, { targetIndex }) {
      Vue.set(state.all, targetIndex, {
        ...state.all[targetIndex],
        selected: false,
      });
    },
  },
  actions: {
    addContributor({ state, commit }, { contributor, stock }) {
      const targetIndex = _.findIndex(state.all, t => t.id === contributor.id);
      if (targetIndex < 0) {
        // 投稿者がない場合
        contributor.hasStocks = [stock.id];
        contributor.selected = false;
        commit('add_contributor', { contributor });
      } else {
        // すでに投稿者がある場合
        const target = state.all[targetIndex];
        if (_.findIndex(target.hasStocks, id => id === stock.id) < 0) {
          commit('count_up', { targetIndex, stock });
        }
      }
    },
    addContributorByStocks({ dispatch }, { stocks }) {
      _.each(stocks, (stock) => {
        dispatch('addContributor', { contributor: stock.user, stock });
      });
    },
    toggleSelected({ state, commit }, { contributor }) {
      const targetIndex = _.findIndex(state.all, c => c.id === contributor.id);
      if (targetIndex >= 0) {
        if (state.all[targetIndex].selected) {
          commit('deselect_contributor', { targetIndex });
        } else {
          commit('select_contributor', { targetIndex });
        }
      }
    },
  },
  getters: {
    all: state => _.orderBy(state.all, [tag => tag.hasStocks.length], ['desc']),
    top10: (state, getters) => _(getters.all).orderBy([contributor => contributor.hasStocks.length], ['desc']).take(8).value(),
    selectedContributors: state => _.filter(state.all, contributor => contributor.selected),
  },
};



