<template>
  <div id="app">
    <div class="layout">
      <the-header />
      <div class="contents">
        <div v-if="$route.name !== 'Welcome' && $route.name !== 'Error'" class="side">
          <the-sidebar />
        </div>
        <div class="main">
          <router-view />
        </div>
      </div>
    </div>
    <the-loading />
  </div>
</template>

<script>
import TheHeader from './components/globals/TheHeader.vue';
import TheSidebar from './components/globals/TheSidebar.vue';
import TheLoading from './components/globals/TheLoading.vue';

export default {
  name: 'app',
  components: {
    TheHeader,
    TheSidebar,
    TheLoading,
  },
};
</script>

<style lang="scss">
@import './styles/reset.scss';
@import './styles/theme.scss';
@import './styles/mixins.scss';

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.layout {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.contents {
  flex: 1;
  display: flex;

  .side {
    width: 260px;
    overflow-y: auto;
    background: shade($primary, 20);
  }

  .main {
    flex: 1;
    overflow-y: auto;
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



