import _ from 'lodash';
import * as config from '../config';

const ValueTypes = {
  ARRAY: 'array',
  OBJECT: 'object',
  NUMBER: 'number',
  STRING: 'string',
};

/**
 * ローカルストレージに値を設定
 * @param key
 * @param val
 * @returns {Promise<any>}
 */
export function setItem(key, val) {
  let valType = '';
  if (_.isArray(val)) {
    valType = ValueTypes.ARRAY;
  } else if (_.isObject(val)) {
    valType = ValueTypes.OBJECT;
  } else if (_.isNumber(val)) {
    valType = ValueTypes.NUMBER;
  } else if (_.isString(val)) {
    valType = ValueTypes.STRING;
  }

  // 配列とオブジェクトはJSON化
  if (valType === ValueTypes.ARRAY || valType === ValueTypes.OBJECT) {
    val = JSON.stringify(val);
  }

  return new Promise((resolve, reject) => {
    try {
      localStorage.setItem(`${config.localStorageNameSpace}_${key}`, val);
      localStorage.setItem(`${config.localStorageNameSpace}_${key}_type`, valType);
      resolve({ key: `${config.localStorageNameSpace}_${key}`, val });
    } catch (e) {
      console.log(e);
      reject(e);
    }
  });
}

/**
 * ローカルストレージから値を取得
 * @param key
 * @returns {Promise<any>}
 */
export function getItem(key) {
  return new Promise((resolve, reject) => {
    try {
      let val = localStorage.getItem(`${config.localStorageNameSpace}_${key}`);
      const valType = localStorage.getItem(`${config.localStorageNameSpace}_${key}_type`);

      if (valType === ValueTypes.ARRAY || valType === ValueTypes.OBJECT) {
        val = JSON.parse(val);
      }

      resolve(val);
    } catch (e) {
      console.log(e);
      reject(e);
    }
  });
}



