export default {
  namespaced: true,
  state: {
    word: '',
  },
  mutations: {
    update_word(state, { word }) {
      state.word = word;
    },
  },
  actions: {
    updateWord({ commit }, { word }) {
      commit('update_word', { word });
    },
  },
  getters: {
    word: state => state.word,
  },
};




