import _ from 'lodash';
import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from './components/views/Home.vue';
import User from './components/views/User.vue';
import Welcome from './components/views/Welcome.vue';
import Error from './components/views/Error.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Home,
    name: 'Home',
  },
  {
    path: '/user',
    component: User,
    name: 'User',
  },
  {
    path: '/welcome',
    component: Welcome,
    name: 'Welcome',
  },
  {
    path: '/error',
    component: Error,
    name: 'Error',
  },
];

const router = new VueRouter({
  routes,
});

/**
 * ルーターのナビゲーションガード設定
 * @param router
 * @param store
 */
export function routerBeforeEach({ store }) {
  router.beforeEach((to, from, next) => {
    if (to.name === 'Welcome') {
      if (_.isEmpty(store.state.user.userId)) {
        store.dispatch('user/loadUserId')
          .then((userId) => {
            if (_.isEmpty(userId)) {
              // userIdがないのでWelcome表示
              next();
            } else {
              // userIdありトップ表示
              router.replace('/');
            }
          })
          .catch(() => {
            // error発生
            router.push('/error');
          });
      } else {
        // userIdありトップ表示
        router.replace('/');
      }
    } else if (to.name === 'Error') {
      next();
    } else if (_.isEmpty(store.state.user.userId)) {
      store.dispatch('user/loadUserId')
        .then((userId) => {
          if (_.isEmpty(userId)) {
            router.replace('/welcome');
          } else {
            next();
          }
        })
        .catch(() => {
          // error発生
          router.push('/error');
        });
    } else {
      next();
    }
  });
}

export default router;



