import * as types from './mutation_types';
import * as storage from '../../utils/localStorage';

export default {
  /**
   * ローカルストレージからユーザーID取得
   * @param commit
   * @returns {Promise<any>}
   */
  loadUserId({ commit }) {
    commit(types.LOAD_STORAGE);
    return storage.getItem('userId')
      .then((response) => {
        commit(types.SET_USER_ID, { userId: response });
        return Promise.resolve(response);
      })
      .catch((e) => {
        e.msg = 'ユーザーIDのロードに失敗しました。';
        return Promise.reject(e);
      });
  },
  /**
   * ユーザーIDをセットする
   * @param commit
   * @param userId
   * @returns {Promise<any>}
   */
  setUserId({ commit }, { userId }) {
    commit(types.LOAD_STORAGE);
    return storage.setItem('userId', userId)
      .then((response) => {
        commit(types.SET_USER_ID, { userId });
        return Promise.resolve(response);
      })
      .catch((e) => {
        e.msg = 'ユーザーIDの保存に失敗しました。';
        return Promise.reject(e);
      });
  },
};



