<template>
  <a href="javascript:;" :class="className" @click="onClick">
    <slot />
  </a>
</template>

<script>
export default {
  props: ['disabled'],
  methods: {
    onClick() {
      if (this.disabled) return;

      this.$emit('click');
    },
  },
  computed: {
    className() {
      const classes = ['button'];
      if (this.disabled) {
        classes.push('disabled');
      }
      return classes;
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../../styles/theme';

.button {
  width: 240px;
  height: 60px;
  line-height: 60px;
  background: $primary;
  color: #fff;
  display: inline-block;
  border-radius: 30px;
  text-align: center;
  font-size: 18px;
  text-decoration: none;

  &:hover {
    opacity: .7;
  }

  &.disabled {
    cursor: default;
    opacity: .4;
  }
}
</style>



