<template>
  <div class="stock-list">
    <ul>
      <li v-for="stock in stocks" :key="stock.id">
        <stock-list-item :stock="stock" />
      </li>
    </ul>
  </div>
</template>

<script>
import StockListItem from './StockListItem.vue';

export default {
  props: ['stocks'],
  components: {
    StockListItem,
  },
};
</script>

<style lang="scss" scoped>
.stock-list {
  ul {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
  }
  li {
    width: 25%;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
  }
  @media screen and(max-width: 1200px) {
    li {
      width: 33.3333%;
    }
  }
  @media screen and(max-width: 800px) {
    li {
      width: 50%;
    }
  }
  @media screen and(max-width: 500px) {
    li {
      width: 100%;
    }
  }
}
</style>



