import Vue from 'vue';
import App from './App.vue';
import router, { routerBeforeEach } from './router';
import store from './store';
import './registerServiceWorker';

Vue.config.productionTip = false;

// ナビゲーションガード設定
routerBeforeEach({ store });

new Vue({
  store,
  router,
  render: h => h(App),
}).$mount('#app');



