<template>
  <div class="welcome">
    <h1>Welcome!</h1>
    <div>
      <p class="lead">「Stoqii.」はQiitaのストック記事を簡単に検索するためのWebアプリケーションです。<br>ユーザーIDを入力してストック検索を始めましょう！</p>
      <form action="" @submit.prevent="onSubmit">
        <div class="form-row">
          <input class="uid-input" type="text" v-model="uid" placeholder="ユーザーID">
        </div>
        <unit-buttons>
          <base-button @click="onSubmit" :disabled="!userId">登録</base-button>
        </unit-buttons>
        <div class="feed-backs">
          <p v-show="loading">Checking...</p>
          <p class="error" v-show="!isAvailable">{{ msg }}</p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import userForm from '../../store/userForm';
import BaseButton from '../parts/BaseButton.vue';
import UnitButtons from '../units/UnitButtons.vue';

const moduleName = 'userForm';

export default {
  data() {
    return {
      uid: '',
    };
  },
  components: {
    BaseButton,
    UnitButtons,
  },
  computed: {
    ...mapGetters(moduleName, ['userId', 'isAvailable', 'loading', 'msg']),
  },
  methods: {
    ...mapActions(moduleName, ['updateUserId', 'checkUserId']),
    onSubmit() {
      this.checkUserId();
    },
  },
  watch: {
    uid(newValue) {
      this.updateUserId({ userId: newValue });
    },
  },
  created() {
    // ページ生成時に動的モジュールを追加
    this.$store.registerModule(moduleName, userForm);
  },
  destroyed() {
    // 動的モジュールの削除
    this.$store.unregisterModule(moduleName);
  },
};
</script>

<style lang="scss" scoped>
h1 {
  font-size: 32px;
  text-align: center;
  margin: 60px 0 30px;
  font-weight: 300;
}
.lead {
  text-align: center;
  margin-bottom: 50px;
}
.form-row {
  text-align: center;
  margin: 20px 0;
}
.uid-input {
  width: 300px;
  padding-right: 8px;
  padding-left: 8px;
  line-height: 40px;
}
.feed-backs {
  margin-top: 20px;
  text-align: center;
}
.error {
  color: #f00;
}
</style>



