<template>
  <a href="javascript:;"
     @click="onClick"
     :class="className">{{ tag.name }}({{ tag.hasStocks.length }})</a>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  props: ['tag'],
  computed: {
    className() {
      const classes = ['tag'];
      if (this.tag.selected) {
        classes.push('selected');
      }
      return classes;
    },
  },
  methods: {
    ...mapActions('tags', ['toggleSelected']),
    onClick() {
      this.toggleSelected({ tag: this.tag });
    },
  },
};
</script>

<style lang="scss" scoped>
a.tag {
  display: inline-block;
  color: #fff;
  background: #666;
  font-size: 12px;
  padding: 5px;
  margin: 2px;
  line-height: 18px;
  text-decoration: none;
  &:hover {
    opacity: .7;
  }
  &.selected {
    background: #af0045;
  }
}
</style>



