import * as types from './mutation_types';

export default {
  [types.SET_USER_ID](state, { userId }) {
    state.userId = userId;
  },
  [types.LOAD_STORAGE](state) {
    state.loading = true;
  },
  [types.LOAD_STORAGE_SUCCESS](state) {
    state.loading = false;
  },
  [types.LOAD_STORAGE_FAIL](state) {
    state.loading = false;
  },
};



